<template>
  <section style="margin-left:15px;margin-right:15px;margin-top: 15px;">
    <el-divider></el-divider>
    <el-descriptions class="margin-top" title="" :column="3" :size="size">
      <template slot="title">
        <span>{{ regedExperiment.name }}</span>
      </template>
      <template slot="extra">
        <router-link v-if="firstID === 'all'" :to="{ name: '模型实验', params: {} }"
          style="text-decoration: none;color: aliceblue;">
          <el-button class="return-list"><i class="el-icon-arrow-left"
              style="margin-right: 5px"></i>返回模型实验列表</el-button>
        </router-link>
      </template>
      <el-descriptions-item label="实验名称">{{ regedExperiment.name }}</el-descriptions-item>
      <el-descriptions-item label="实验编号">
        {{ regedExperiment.experiment_id }}
      </el-descriptions-item>
      <el-descriptions-item label="Lifecycle Stage">
        {{ regedExperiment.lifecycle_stage }}
      </el-descriptions-item>
      <el-descriptions-item label="工件路径">
        {{ regedExperiment.artifact_location }}
      </el-descriptions-item>
      <el-descriptions-item label="标签">
        {{ regedExperiment.tags[0] || '暂无' }}
      </el-descriptions-item>
    </el-descriptions>

    <el-collapse v-model="activeNames" @change="handleChange">
      <el-collapse-item title="模型实验描述" name="1">
        <span v-if="descvisalble && regedExperiment.tags.length > 1 && regedExperiment.tags[1].key === 'mlops.framework'">{{
          regedExperiment.tags[1]["value"]
        }}&nbsp;&nbsp;</span>
        <span
          v-else-if="descvisalble && regedExperiment.tags.length > 0 && regedExperiment.tags[0].key === 'mlops.framework'">{{
            regedExperiment.tags[0]["value"]
          }}&nbsp;&nbsp;</span>
        <span v-else v-show="descvisalble">暂无&nbsp;&nbsp;</span>
        <el-button v-if="descvisalble" type="text" @click="handleEdit"><i class="el-icon-edit"></i></el-button>

        <el-input v-show="descEditable" type="textarea" style="width:60%" :rows="3"
          v-model="editExperimentMeta.description"></el-input>
        <div>
          <el-button type="text" v-show="descEditable" @click="handelCancel" style="margin-right: 0px;"
            icon="el-icon-arrow-left">取消</el-button>
          <el-button style="align:right" v-show="descEditable" type="text" icon="el-icon-check" @click="submitSave"
            circle>保存</el-button>
        </div>
      </el-collapse-item>
      <el-collapse-item title="Runs列表" name="2">
        <!--列表-->
        <el-table :data="modelruns" highlight-current-row v-loading="listLoading" @selection-change="selsChange"
          style="width: 100%;">
          <el-table-column type="selection" min-width="5%">
          </el-table-column>
          <el-table-column prop="info.run_id" label="Run ID" min-width="25%" sortable show-overflow-tooltip>
            <template slot-scope="scope">
              <img src="@/assets/icon-yes.svg" />
              <router-link
                :to="{ name: '模型实验详情', params: { run_id: scope.row.info.run_id, experiment_id: scope.row.info.experiment_id } }"
                style='text-decoration: none'>
                {{ scope.row.info.run_id }}
              </router-link>
            </template>
          </el-table-column>
          <el-table-column prop="info.start_time" label="开始时间" min-width="20%" sortable show-overflow-tooltip>
            <template slot-scope="scope">
              <timeago :datetime="scope.row.info.start_time" locale="zh-CN"></timeago>
            </template>
          </el-table-column>
          <el-table-column prop="info.lifecycle_stage" label="Lifecycle Stage" min-width="15%" sortable
            show-overflow-tooltip>
          </el-table-column>
          <el-table-column prop="data" label="责任人" min-width="20%" sortable>
            <template slot-scope="scope">
              <el-tag v-if="scope.row.data.tags.length > 0">{{
                scope.row.data.tags[0].value
              }}</el-tag>
              <el-tag v-else>{{
                无
              }}</el-tag>
            </template>
          </el-table-column>
          <el-table-column prop="info.status" label="当前状态" min-width="25%">
          </el-table-column>
        </el-table>
        <el-col :span="24" class="toolbar">
          <el-pagination layout="prev, pager, next" @current-change="handleCurrentChange" :page-size="20"
            :page-count="total" style="float:right;">
          </el-pagination>
        </el-col>
      </el-collapse-item>
    </el-collapse>
  </section>
</template>

<script>
// {{ regedExperiment.creation_timestamp | formatDate}}
import { getExperiment, getModelRuns, EditExperimentDesc } from '@/api/api'
import moment from 'moment'
export default {
  data () {
    return {
      modelruns: [],
      regedExperiment: {},
      // edit_btn: true,
      descvisalble: true,
      descEditable: false,
      editExperimentMeta: {
        description: ''
      },
      activeNames: ['1', '2'],
      firstID: 'all',
      altListData: [],
      moment: moment,
      listLoading: false,
      filters: {
        name: ''
      },
      total: 0,
      page: 1,
      sels: [] // 列表选中列
    }
  },
  filters: {
    formatDate: function (value) {
      let date = new Date(value)
      let y = date.getFullYear()
      let MM = date.getMonth() + 1
      MM = MM < 10 ? ('0' + MM) : MM
      let d = date.getDate()
      d = d < 10 ? ('0' + d) : d
      let h = date.getHours()
      h = h < 10 ? ('0' + h) : h
      let m = date.getMinutes()
      m = m < 10 ? ('0' + m) : m
      let s = date.getSeconds()
      s = s < 10 ? ('0' + s) : s
      return y + '-' + MM + '-' + d + ' ' + h + ':' + m + ':' + s
    }
  },
  watch: {
    isall () {
      this.getModelVersionsInfo(this.isall)
      // this.changeHost()
      // this.drawLine()
    }
  },
  methods: {
    // 获取模型版本信息
    getRuns () {
      let self = this
      let params = {
        user_name: JSON.parse(sessionStorage.getItem('name')),
        experiment_id: this.$route.params.experiment_id
      }
      console.log(params, 'paraar')
      // let headers = {Authorization: 'Token '+JSON.parse(sessionStorage.getItem('token'))};
      let headers = {
        'Content-Type': 'application/json',
        Authorization: 'Token ' + JSON.parse(sessionStorage.getItem('token'))
      }
      getModelRuns(headers, params).then((res) => {
        self.listLoading = false
        let { msg, code, data } = res
        if (code === '999999') {
          self.modelruns = data
          console.log(self.modelruns, 'self.modelruns')
        } else {
          self.$message.error({
            message: msg,
            center: true
          })
        }
      })
    },
    // 获取模型元数据信息
    getExperimentMetaData () {
      let self = this
      let params = {
        user_name: JSON.parse(sessionStorage.getItem('name')),
        experiment_id: this.$route.params.experiment_id
      }
      console.log(params, 'paraar')
      // let headers = {Authorization: 'Token '+JSON.parse(sessionStorage.getItem('token'))};
      let headers = {
        'Content-Type': 'application/json',
        Authorization: 'Token ' + JSON.parse(sessionStorage.getItem('token'))
      }
      getExperiment(headers, params).then((res) => {
        self.listLoading = false
        let { msg, code, data } = res
        if (code === '999999') {
          self.regedExperiment = data
          console.log(self.regedExperiment, 'self.regedExperiment')
        } else {
          self.$message.error({
            message: msg,
            center: true
          })
        }
      })
    },
    // 提交模型描述编辑
    submitSave: function () {
      let self = this
      // NProgress.start();
      let params = JSON.stringify({
        experiment_id: self.$route.params.experiment_id,
        description: self.editExperimentMeta.description,
        user_name: JSON.parse(sessionStorage.getItem('name'))
      })
      console.log(params, 'params')
      let header = {
        'Content-Type': 'application/json',
        Authorization: 'Token ' + JSON.parse(sessionStorage.getItem('token'))
      }
      EditExperimentDesc(header, params).then(_data => {
        let { msg, code } = _data
        if (code === '999999') {
          self.$message({
            message: '添加成功',
            center: true,
            type: 'success'
          })
          this.descEditable = false
          this.descvisalble = true
          this.editExperimentMeta.description = ''
          self.getExperimentMetaData()
        } else if (code === '999997') {
          self.$message.error({
            message: msg,
            center: true
          })
        } else {
          self.$message.error({
            message: msg,
            center: true
          })
          this.editExperimentMeta.description = ''
          this.descEditable = false
          this.descvisalble = true
          self.getExperimentMetaData()
        }
      })
    },
    handelCancel: function () {
      this.descEditable = false
      this.descvisalble = true
      this.editExperimentMeta.description = ''
    },
    handleEdit: function () {
      this.descEditable = true
      this.descvisalble = false
      if (this.regedExperiment.tags.length > 0) {
        this.editExperimentMeta.description = this.regedExperiment.tags[0].key + ':' + this.regedExperiment.tags[0].value
      } else {
        this.editExperimentMeta.description = '暂无'
      }
    },
    handleChange (val) {
      console.log(val)
    },
    handleCurrentChange (val) {
      this.page = val
    }
  },
  mounted () {
    this.getExperimentMetaData()
    this.getRuns()
    // this.getModelVersionsInfo(this.isall)
    // if (this.$route.params.firstID) {
    //   this.firstID = this.$route.params.firstID
    // } else {
    //   this.firstID = 'all'
    //  }
    // this.firstID = this.$route.params.firstGroup
  }
}
</script>

